export default function SzopkVsTop(){

    useEffect(() => {
        PageViews.trackPageView('VS Top');
    }, [])

    return <>
    
        <Helmet>
            <title>VS Top</title>
            <meta
                name="description"
                content="Tbor ochrancov prrody a krajiny"
            />
            <meta 
                name="author" 
                content="Dalibor Kundrat" 
            />
            <meta
                name="keywords"
                content="netopiere, non odchyt, ochrana prrody, elmy, ttna ochrana prrody, tbor ochrancov prrody"
            />
            <meta
                property="og:title"
                content="VS TOP"
            />
            <meta 
                property="og:description" 
                content="Leto 2022 prinieslo stretnutie ochrancov prrody a krajiny v Kopytovskej doline pri obci indliar" 
            />
            <meta
                property="og:image"
                content="https://damikun.github.io/wildlife/og_image_top.png"
            />
            <meta property="og:type" content="article" />
        </Helmet>

        <div className="space-y-5 text-base lg:text-lg">

            <LoadableImage className="rounded-sm" src={GetUri("g76l1j4crgsyy8e/10_TOP.webp")} alt="TOP brana"/>

            <h1 className="text-xl font-semibold">VS Top</h1>

            <p>Ako tomu bva u dlh roky aj leto 2022 prinieslo stretnutie ochrancov prrody a krajiny tentokrat v <i className="italic">Kopytovskej doline</i> nealeko obce <i className="italic">indliar.</i></p>

            <p>Tdennej stanovaky s bohatm programom sa zastnilo vye 300 ud v kempe <i className="italic">Zlat Kopyto</i>.</p>

            <p>Denn innos bola rozdelen do niekokch sekcii poda oboru innosti. Niektor sa kli pestrosti programu aj spojili.</p>

            <ul className="flex flex-row flex-wrap whitespace-nowrap list-disc list-inside w-full italic">
                <li className="pr-4">krajinrska</li>
                <li className="pr-4">mykologick</li>
                <li className="pr-4">lesncka</li>
                <li className="pr-4">botanick</li>
                <li className="pr-4">ornitologick</li> 
                <li className="pr-4">herpetologick</li>
                <li className="pr-4">praktick</li>
                <li className="pr-4">detsk</li>
                <li className="pr-4">geologick</li>
                <li className="pr-4">entomologick</li>
                <li className="pr-4">chiropterologick</li>
            </ul>

            <p>Nstup do sekcie bol o 9:00 a konilo sa zvyajne v poobedajch hodinch. Niektor svoju innos vykonvali aj vo veernch/nonch hodinch</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("7uwe8gfskv3k5x4/01_TOP.webp")} alt="TOP nastup do sekcii"/>

            <LoadableImage className="rounded-sm" src={GetUri("dgvuqbo7hhy8gbg/16_TOP.webp")} alt="Navrat z monitoringu"/>

            <p>Jednou zo skupn bol aj <b className="semibold">monitoring eliem</b>. Na vybranom zem dolo k rozmiestneniu fotopasci a poas dennch vjazdov sa zbierali a analyzovali stopy a pobytov znaky. Na fotke niie sa rozobera jazve trus.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("yrbd6qcdw2hguo6/02_TOP.webp")} alt="Stopy seliem"/>

            <p>Prv dni sprevdzali veern prehnky. To napomohlo stopovaniu nakoko premoen podklady poskytli mnostvo erstvch a autentickch otlakov.</p>

            <p>V oblasti sa nachdza <b className="semibold">migran trasa vlkov</b>. To sa aj potvrdilo vskytom mnostva trusu, mou a taktie mnostvom stp.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("9wejp78eaps2oef/03_TOP.webp")} alt="Stopa vlka"/>

            <p>Trus obsahoval napr. zbytkov asti po strhnutej zvery ako chrupavky, mal kosti a samotn srs. Musm poznamena, e osobne som tak vek nlo od vka ete nevidel. Hne dostvam info, e trus me ma aj do 10kg.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("mn9d7sj4pvna5a7/06_TOP.webp")} alt="Trus vlka"/>

            <LoadableImage className="rounded-sm" src={GetUri("gm2h4nn2q1r05az/07_TOP.webp")} alt="Trus vlka"/>

            <p>V oblasti ma zven aktivitu aj <b className="semibold"><i className="italic">Rys Ostrovid</i></b>. To sa taktie potvrdilo poetnm vskytom pobytovch znakov.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("zedpx4y8t5jxtim/05_TOP.webp")} alt="Rys stopy"/>

            <LoadableImage className="rounded-sm" src={GetUri("gt22hg8y09x220g/04_TOP.webp")} alt="Rys stopa detail"/>

            <p>Miestne podmienky s pre jeho pobyt vyhovujce. Mnostvo malch skalnch tvarov, hust lesy a strm svahy mu poskytuj vhodne toisko.</p>

            <LoadableImage className="rounded-sm" src={GetUri("059bhzw6hs8lsbw/34_TOP.webp")} alt="Smrekovica"/>

            <LoadableImage className="rounded-sm" src={GetUri("3vv4toyxf5sp6xd/08_TOP.webp")} alt="Les"/>

            <p>Taktie sa potvrdil vskyt <b className="semibold">malho potu medveov</b>, ktor vo vke nad 1000mnm niekde na hranici lesa a kosodreviny nachdzaj bohat stravu ako maliny, uoriedky i brusnice (okolie Smrekovice a oblast okolo hlavnho hrebea Braniska). Niesu tu premonoen a k pozorovaniu dochdza vnimone.</p>

            <p>Vetky nazbieran vzorky sa bud nsledne analyzova, u kosti djde k oisteniu a nsledne skmaniu prtomnch otlakov zubov od predtora. Na fotke niie je lebka mladho jazveca.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("30zace1ut8ivdbu/09_TOP.webp")} alt="Jazveia hlava"/>

            <p><b className="semibold">Poty jelenej zvery</b> s niie. Trus a pobytov znaky napovedaj, e v oblasti takmer plne prevldaj jelenice, obas je k videniu mlad jele, no korunov jelene sa tu nenachdzaj. Tento jav je poda mojich sksenosti pomerne asty a jelene sa tak bud pravdepodobne nachdza vo vekch riedach na vzdialenom pohor (Hrebe Braniska). Mnostvo vytench stromov od dospelch samcov bolo takmer nulov.</p>
        
            <LoadableImage className="rounded-sm" src={GetUri("j26u5t1fzzhsz9e/11_TOP.webp")} alt="Jelenica a mlady jelen"/>

            <LoadableImage className="rounded-sm" src={GetUri("7gipbm2o5g98otc/12_TOP.webp")} alt="Jelenice"/>

            <LoadableImage className="rounded-sm" src={GetUri("k7shq9f6mo42dmh/36_TOP.webp")} alt="Jelenice"/>

            <p><b className="semibold">Vskyt srnej zvery</b> bol v nich potoch, ako bvam zvyknut. Je to urite aj vplyvom predatorov, najme poetnho Rysa Ostrovida, ale taktie, aj miestnou formou manamentu zvery a obhospodarovanim pdy. Nikde v alekom okol nedochdza k ponohospodrskej innosti. Lky sa vyuvaj len na pastvu oviec a ako zdroj sena. Aj tto skutonos iastone vlva na poetnos vysokej zvery, nakoko disponuje len prirodzenou potravou.</p>

            <p>Je nutne taktie poznamena, e vysok zver je momentlne (jl/august) stiahnut niie kli nadmernmu suchu. V niektorch reginoch s dokonca hlsen vek riedy, ktor s sprievodnm javom sucha v danej oblasti. Zver sa potom dr nealeko stlych vodnch tokov.</p>

            <p><b className="semibold">Chiropterologick</b> sekcia monitorovala netopierov, oho sasou bol aj odchyt v lokalitch ako jakyne, jazera i kostoln vee/podkrovia. Psobeniu tejto skupiny sa venujem v samostatnom reporte z veernho  <a target="_blank" rel="noreferrer" className="italic text-gray-300 hover:text-blue-300" href="http://localhost:3000/#/Story/BatNightMonitoring">odchytu pri miestom jazierku</a>.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("px7kf6qjmy37jcv/IMG_9366.jpg")} alt="Odchyteny netopier"/>

            <LoadableImage className="rounded-sm" src={GetUri("ep8rdxa572wlz1n/IMG_9396.jpg")} alt="Odchyteny netopier"/> 
            
            <br/>
            <p><b className="semibold">Entomolgovia</b> (Hmyz) si taktie prili na svoje. Ta rados v hlase, ke poujete z lky kria <i className="italic">"Maam ho"</i> to som ete nezail. Ta eufria nastala pri pohade na pre ns obyajnho konka no pre nich vzcny druh so snenm vskytom.</p>

            <p>Zaujmav bol prve konk ervenokrdly <i className="italic">(Psophus stridulus)</i>, ktor navonok vyzer obyajne no zaujme zafarbenm krdel aj letom samotnm.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("b63w0i951yjegu8/15_TOP.webp")} alt="Konik cerveno kridly - Psophus stridulus"/> 

            <LoadableImage className="rounded-sm" src={GetUri("y4bc4wuy0ahndrp/13_TOP.webp")} alt="Konik cervenokridly kridla - Psophus stridulus"/> 

            <LoadableImage className="rounded-sm" src={GetUri("jtw2i3d7cmndgww/14_TOP.webp")} alt="Konik cervenokridly- Psophus stridulus"/> 

            <p>Skupina so zemeranm na motov odchytvala vybran druhy pas da aj cez noc na nealekej lke. Odobrat vzorky konzervovali pomocou etanolu.</p>

            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("748030rvga0b1ac/37_TOP.webp")} alt="Motyl"/> 

            <LoadableImage className="rounded-sm" src={GetUri("eqbd5ohx7fvsheb/18_TOP.webp")} alt="Nocny odchyt hmyzu"/> 

            <LoadableImage className="rounded-sm" src={GetUri("7cn8xtpaazjszqg/19_TOP.webp")} alt="Nocny odchyt motylov"/> 

            <p>Na svoje si prili aj deti, ktor v rmci detsk sekcie stavali naprklad bdky pre netopiere, alebo navtvili nealek jaskyu <i className="italic">Zla diera</i>.</p>

            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("bep6gn444idnglj/20_TOP.webp")} alt="Jaskyna Zla diera"/> 
            <LoadableImage className="rounded-sm" src={GetUri("bgbt6s8bkuc48yp/22_TOP.webp")} alt="Jaskyna Zla diera"/> 
            <LoadableImage className="rounded-sm" src={GetUri("vmfzfldm0m8qfin/21_TOP.webp")} alt="Jaskyna Zla diera"/> 

            <p>Starie absolvovali naprklad tru s prespanm na vrchu smrekovica.</p>
            <LoadableImage className="rounded-sm" src={GetUri("22bv8yvyyvbver1/33_TOP.webp")} alt="Vylet dievcata smrekovica"/> 
            <LoadableImage className="rounded-sm" src={GetUri("jm5dozq2upxjbfl/31_TOP.webp")} alt="Vylet mladez smrekovica"/>
            <LoadableImage className="rounded-sm" src={GetUri("rxytri1osz0mgpx/35_TOP.webp")} alt="Vylet mladez smrekovica"/> 

            <p>Kad veer pri zhromaden jednotliv sekcie zhodnotili dan de a prezentovali pln na de nasledujci. Nechbala ani iv hudba a zbava.</p>

            <LoadableImage className="rounded-sm" src={GetUri("p2pqkmusx3bgmpq/23_TOP.webp")} alt="Zhodnotenie dna"/> 
            <LoadableImage className="rounded-sm" src={GetUri("entlimkfj1twu89/24_TOP.webp")} alt="Taborove zhromazdenie"/>
            <LoadableImage className="rounded-sm" src={GetUri("jm2y9vjps1940oa/25_TOP.webp")} alt="Taborove zhromazdenie"/>

            <p>Ako tomu bva vetkmu je raz koniec a po tdni sa to zakonilo spolonm guaom, ktory sme navarili.</p>

            <LoadableImage className="rounded-sm" src={GetUri("x0fse4a1fsamt01/26_TOP.webp")} alt="Priprava gulasu"/>
            <LoadableImage className="rounded-sm" src={GetUri("8z2d3anl01teyyu/27_TOP.webp")} alt="Priprava gulasu"/> 
            <LoadableImage className="rounded-sm" src={GetUri("nfsied6c2owwchj/28_TOP.webp")} alt="Kotlikovy majsteri"/> 
            <LoadableImage className="rounded-sm" src={GetUri("8vqt54jgoqau8nz/32_TOP.webp")} alt="Kotlikovy majsteri"/> 
            <LoadableImage className="rounded-sm" src={GetUri("f5my0x8rspup6ie/29_TOP.webp")} alt="Varenie gulasu"/>
            <LoadableImage className="rounded-sm" src={GetUri("0k8e5rvdjs52sct/30_TOP.webp")} alt="Gulas"/>
        </div>
    </>
}