import { useEffect } from "react";
import { Helmet } from 'react-helmet';
import { GetUri } from "../../Constants";
import LoadableImage from '../UI/LodableImage';
import { PageViews } from "@piwikpro/react-piwik-pro";

export default function BatNightMonitoring(){

    useEffect(() => {
        PageViews.trackPageView('Nony odchyt netopierov');
    }, [])
    
    return <>

        <Helmet>
            <title>Nony odchyt netopierov</title>
            <meta
                name="description"
                content="Non odchyt netopierov"
            />
            <meta 
                name="author" 
                content="Dalibor Kundrat" 
            />
            <meta
                name="keywords"
                content="netopiere, non odchyt, ultrazvuk, vek netopiera, ochrana netopierov, netopiere.sk, odchytov siete"
            />
            <meta
                property="og:title"
                content="Non odchyt netopierov"
            />
            <meta 
                property="og:description" 
                content="Na stretnut ochrany prrody a krajiny v kopytovskej doline sa jeden veer uskutonil non odchyt netoprieov SON." 
            />
            <meta
                property="og:image"
                content="https://damikun.github.io/wildlife/og_image_odchyt_netopierov.png"
            />
            <meta property="og:type" content="article" />
        </Helmet>

        <div className="space-y-5 text-base lg:text-lg">

            <h1 className="text-xl font-semibold">Non odchyt netopierov</h1>

            <p>Na stretnut ochrany prrody a krajiny v kopytovskej doline sa jeden veer uskutonil non odchyt netoprieov v spoluprci s <a target="_blank" rel="noreferrer" className="italic text-gray-300 hover:text-blue-300" href="https://www.netopiere.sk">netopiere.sk</a> (SON - Spolonos pre ochranu netopierov) </p>
        
            <p>Jedna sa o formu monitoringu, kde sa tieto cicavce zachytvaj pomocou pecilnej sieti (nrazova sie) v mieste ich veernho vskytu. Hlavn aktivita je niekde medzi 20-23h (plvajca)</p>

            <p>V tomto prpade boli siete rozmiestnen v tvare pisanemna <i>"L"</i> po obvode malho lesnho jazera. Oblas kde lovia mono oznai ako <i className="italic">lovny biotop</i>. Prve lesn oblsati s z hadiska monitoringu zaujmav, nakoko mu by domovom vzcnych druhov.</p>

            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("4xbbiheomgqnmtm/IMG_9423.jpg")} alt="Rozmiestnenie sieti"/>

            <p>Po rozmiestneni sieti a vzdialeniu sa na cca. 10m od odchytovej zny, zapname analyztor pre ultrazvukov zznam.</p>

            <p>K tomu dnes sli obyajn telefn s externym  <a target="_blank" rel="noreferrer" className="italic text-gray-300 hover:text-blue-300" href="https://www.bat-man.sk/ultrazvukovy-detektor-na-netopiere-wildlife-acoustic-echo-meter-touch-2--ios/">detektorom a aplikcia</a> , ktor nsledne nameran signl spracuje.</p>

            <p>Spracovan signl je mon sledova v relnom ase a taktie pova niekolko-nsobn spomalenie a transormciu do nho zvukovho spektra. Nazvime to <i className="italic">zvukovy preklad</i>. To vetko sa deje poas letu netopiera.</p>

            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("a05sf2adh0ibqq1/IMG_9228.jpg")} alt="Analyzator ultrazvukoveho spektra"/>

            <p>Kad z druhov sa vyznauje pecifickm zvukom a frekvenciou (20-110khz), ktor pouva. Je nutne poznamena, e ultrazvukov spektrum pouvaj pre let a s nim spojen orientciu nie pre ben prejavy a komunikciu (socilne prejavy)</p>

            <p>Aplikcia dokonca, po krtkej dobe zznamu napovie, ak druhy netopierov zachytila. Sksen pozorovatelia, to dokonca niekedy vedia odhadn na zklade zvukoveho prekladu alebo frekvennho sla. Je nutn bra do vahy urit mieru chybovosti, a preto sa vetko vyhodnocuje aj dodatone.</p>

            <p>Svetla sme zhasli no v kude sa bavme. Netopiere ns vnmaj a pri prelete sa nm vyhn. Cca. po 30 mintach akania detektor zaznamenal prelet nekolkych kusov, zapname elovky a mierime k odchytovym sieam.</p>

            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("kqmwl8ye1j94uqd/IMG_9231.jpg")} alt="Odchyteny netopier"/>

            <p>Netopiere prelietavaju astokrt v skupine. Aj v naom prpade dolo k odchytu niekoko kusov rovnakho druhu za krtky as.</p>

            <p>Zachyt postupne odoberme pomocou koench rukacivc zo sieti. Je nutne zvi pozornos nakoko maj pomerne ostr zuby a asto reaguj kusanim a kriekanim.</p>

            <p>Po uvoneni zo siete sa zisuje pohlavie a vvojov tdium. Sleduj sa tie defekty a neprirodzen vvoj i pokodenie krdel.</p>

            <p>V tomto prpade bolo odchytench niekoko netopierov druhu <i className="italic">Myotis</i></p>

            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("s5tmkjkx8y9jzuj/IMG_9356.jpg")} alt="Odchyteny netopier"/>

            <LoadableImage className="rounded-sm" src={GetUri("ujdqfv1uiyehchs/IMG_9328.jpg")} alt="Odchyteny netopier"/>

            <LoadableImage className="rounded-sm" src={GetUri("uaxbqidprl3so2o/IMG_9358.jpg")} alt="Odchyteny netopier kridlo"/>
            <br/>
            V krdle mete pozorova pvodn prsty (hrub iary) a jemn cievy ktormi v blanch prdi netopierom krv. Pouvaj to aj na chladenie krvnho obehu v teplch doch, ke mavju krdlami naprzdno.
            <br/>
            <LoadableImage className="rounded-sm" src={GetUri("px7kf6qjmy37jcv/IMG_9366.jpg")} alt="Odchyteny netopier"/>

            <LoadableImage className="rounded-sm" src={GetUri("9hjcozgirt6rulj/IMG_9373.jpg")} alt="Odchyteny netopier"/>

            <LoadableImage className="rounded-sm" src={GetUri("ep8rdxa572wlz1n/IMG_9396.jpg")} alt="Odchyteny netopier"/> 
            
            <p>Monitoring trv 2-3h a s pribdajcim asom intenzita preletov kles, preto balme, sadme do aut a vraciame sa do campu da si pivo a sp.</p>

        </div>
    </>
}