import { useEffect } from "react";
import { Helmet } from 'react-helmet';
import { GetUri } from "../../Constants";
import LoadableImage from '../UI/LodableImage';
import { PageViews } from "@piwikpro/react-piwik-pro";

 export default function DroughtMonitoring(){

    useEffect(() => {
        PageViews.trackPageView('Monitoring zvery v obdob sucha');
    }, [])

    return <>

        <Helmet>
            <title>Monitoring zvery v obdob sucha</title>
            <meta
                name="description"
                content="Monitoring zvery v obdob sucha"
            />
            <meta name="author" content="Dalibor Kundrat" />
            <meta
                name="keywords"
                content="vysoka zver, sucho, jelene, jelene sucho, jelene kukurica"
            />
            <meta
                property="og:title"
                content="Monitoring zvery v obdob sucha"
            />
            <meta 
                property="og:description" 
                content="Leto 2022 sebou priniesolo obdobie sucha. Rozhodol som sa prve poas najvesich teplotnch extrmov monitorova vybran zemie a chovanie jelenej zvey." 
            />
            <meta
                property="og:image"
                content="https://damikun.github.io/wildlife/og_image_monitoring_sucha.png"
            />
            <meta property="og:type" content="article" />
        </Helmet>

        <div className="space-y-5 text-base lg:text-lg">

            <h1 className="text-xl font-semibold">Monitoring zvery v obdob sucha</h1>

            <p>Leto 2022 sebou priniesolo obdobie sucha. Rozhodol som sa prve poas najvesich teplotnch extrmov monitorova vybran zemie a chovanie jelenej zvery.<span className="text-sm ml-1">(Vsledky krtko zhodnotm na konci textu)</span></p>
        
            <p>Na vybranom mieste u vye mesiaca niesu zaznamenan zrky ak zanedbm jeden a dva prpady a nameranch 0.5mm v najblizsej mernej stanici. To napoved, e v oblasti je extrmne sucho.</p>
            <p>Lokalitu dobre poznm no naposledy som ju monitoroval v zime, ke sa tam nachdzalo cca 20KS jelenej zvery a odhadom asi 50kusov srnej. Jelene tu prezimovali v riede od mladch a po starie kusy. Lane tu, ale v zime k pozorovaniu neboli. Populcia srnie zvery bola na vekos okolitch leskov obrovsk a koncentrovala sa na jedno miesto.</p>
            <p>Osobne som predpokladal, e jelene sa v tejto lokacii tandardn v lete nevyskytuj, mono tak sporadicky no pestros potravy ma presvedila o opaku.</p>

            <p>Jelene tu maj v okol leskov k dispozcii repkove, kukurin a penin pole. Obzvl kukurica poskytuje potravu bohat na vodu, nakoko rastie aj poas povrchovho sucha a nema tendenciu vysycha</p>
            
            <p>V skorch rannch hodinch pozorujem v repke pravidelne niekoko kusov jeleov. Pred vchodom slnka sa premiestuj na kukurin pole, kde op s nadenim jedia, o vidia.</p> 

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("7n2amztsbnprp89/08.webp")} alt="Ranne jelene kukurica"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("2ute8sk1oi39nzh/35.webp")} alt="Ranne jelene kukurica"/>

            <p>Nefotm a nepribliujem sa za kad cenu. Pozorujem ich sprvanie a ktorm smerom sa vyberu.</p>

            <p>Asi po 40mintach pomaly smeruj dolu k okraju lesa. Miesto kam id poznm. Plnujem, e sa sem vrtim.</p>

            <h4 className="font-semibold"># Podvecer</h4>

            <p>O piatej s obrovskm asovm predstihom sa vraciam na kukurin pole. Chcel som sa toti na mieste rozhodn poda slnka a smeru vetra kam sa zaijem.</p>

            <p>Volm vyvyene stanovisko nealeko miesta, kde rno zaliezli do lesa. V batohu mm jedlo a hlavne vodu. Vravm si tak toto je aj namna privea 31 v tieni</p>

            <p>Asi o 18:00 sa objavuje na horizonte paroh. Sm tomu neverm. Je to voko priskoro. (Zpad je o 21:00) Za asi 5 mint u vidm cca 8KS jeleov vychdzajcich z leoviska na kukurin pole.</p>
            
            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("4hgzbovvshflg0h/07.webp")} alt="Skora jelenia dvojica"/>

            <div className="w-full justify-center content-center">
                <iframe
                    className="w-full h-80 md:h-96 xl:h-128"
                    src="https://www.youtube.com/embed/qk9LT7qp3hA" 
                    title="Starsie jelene predvecerom" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen />    
            </div>
            
            <p>Id, ako na promende zvolil som si dobre no postupne asi do 30min jeden po druhom zas vchdzaj do lesa. Preli z av do prva. V prvom rade by som poznamenal, e v takom ase a teplotch jelene vonku tandardn neuvidte. Tern a smer pohybu napoved, e proste boli smdn a nakoko veina mlk, behnisk a rh je plne vyschla, nemaj vea monosti kam chodi pi.</p>

            <p>akm i neprdu alie no do veera je pole przdne, vrtim sa, teda rno.</p>

            <h4 className="font-semibold"># Rano</h4>

            <p>Je 5:00 a ja prichdzam na kopec. V repke vidie mladie jelene, ktor zachdzaj do lesa. Po prchode na kukurin pole pozorujem alie kusy a takmer identicky scenr ako naposledy. Jelene zalez na to iste miesto.</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("ladqczb5a8xti6b/12.webp")} alt="Mlady jelen v repkovom poli"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("8hmp380aedzjzf0/25.webp")} alt="Ranne jelene"/>

            <p>Rozhodujem sa, e pred veerom pjdem bliie obhliadnu miesto, kde zaliezaj s vedomm, e mu niekde v okol polihavat a vymam stratgiu, ako sa neprezradi v opanom prpade by som ich na niekoko dni mohol strati z dohadu a hada ich po celom okol nebudem.</p>

            <h4 className="font-semibold"># Podvecer</h4>

            <p>Op cca. pred 6tou veer prichdzam na kukurin pole a sadm si do trvnatej ryhy na okraji poa a lesa. Je neskutone teplo...  Sedmokrsky v okol zanechvaj prjemn armu a ja zobem zrno, ktor tu nadivoko rastie ako preitok z minulch rokov.</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("s5e6upvpn84v3i7/39.webp")} alt="Ranne jelene"/>
            
            <p>Dnes vyla vysok pomerne neskoro. Ako prv to boli jelenice jedna dokonca s tohtoronm jelenaom, ktor bolo u pekne odrastene.</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("sxqgvju72ix5gbk/37.webp")} alt="Skora jelenia dvojica"/>

            <p>Jelene sa ukzali a pred 9tou veer, ke som sa pomaly vracal sp</p>

            <p>Cestou som ete natrafil na srnu, ktor akurt kmila mlad srna, a tak polhodinu akam a ho dokmi, aby som ich zbytone neplail nakoko su nealeko cesty kade kram</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("yoftod9605zlaey/38.webp")} alt="Srna a srnca"/>

            <p>Tu je potrebn zdrazni, e vyruenie by mohlo ma pri takomto teplom poas za nsledok hyn mlat nasledkom dehydratcie. Srnat s prv mesiac uplne zvisl na <i className="italic">"cicani"</i> a vyruena srna sa niekedy koly bezpeiu vrti a na alie krmenie, o me by na druhy de rno alebo na obed.</p>
            
            <h4 className="font-semibold"># al de veer</h4>

            <p>Prichdzam na rovnak miesto. Pohodlie v ryhe z predolho dna nebolo idelne, tak pad rozhodnutie, e sa vyvalm priamo v kukurici. Hadm dostatone vysok rastliny, ktor ma zakryj z kadej strany a akm na zpad slnka.</p>

            <LoadableImage  className="rounded-sm shadow-sm h-96 xl:h-128 object-cover rotate-0" src={GetUri("amr0aqrlqcismro/40.webp")} alt="Stanovisko v kukurici"/>
            
            <LoadableImage  className="rounded-sm shadow-sm h-96 xl:h-128 object-cover" src={GetUri("0x72e00hm0wwgiu/50.webp")} alt="Obdobie sucha"/>

            <p>as ubieha pomaly a ja premam. Je 8 veer. Som smdn a hladn vetko, o som si zobral som zmastil a vravm si, e tomu dm max. 15 mint a pjdem. Nebudem tu za kad cenu.</p>

            <p>Po 15mintach velm na odchod zdvham hlavu a hne ju dvam nazad dole, ke zbadm, e asi 15m predomnou je niekoko jeleov. Fascinujce obas som nieo ctil no nebolo ich zo zaiatku vbec pou a u vbec vidie.</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("s5ddq192rxj77in/10.webp")} alt="Jelen detail kukurica"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("jfp1n5dgvt8hp2o/42")} alt="Jelen zerie kukuricu"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("1acvwnnpf06iszx/43.webp")} alt="Jelen a lan v kukurici"/>

            <p>Robm par zberov, dokonca zauj uzvierku a chvu krtia hlavou a pozoruj ma. Mam maskovanie nevedia ma identifikova navye polyhavam... Netrv to dlho a zase s ponoren do prevania kukurice. Vbec sa s tm neser zoer cel spodn as rastliny. Potichu sedm a len povam chrumkanie obas zavanie ich prenikav pach.</p>

            <div className="w-full justify-center content-center">
                <iframe
                    className="w-full h-80 md:h-96 xl:h-128"
                    src="https://www.youtube.com/embed/ShIkUNKeZMk" 
                    title="Jelene v kukuricnom poli" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen />    
            </div>

            <p>Asi po hodine posedavania sa zaali pomaly presva do stredu poa a ja mem v kude po okraji lesa ods pre.</p>

            <p>Takto som ich monitoroval ete alie 2 dni. Niektor jedince som si pomenoval, pretoe som ich stretval pravidelne in boli v dan de novikmi. Vidie je vetko star, mlad jelene, lane aj erstv tohtoron vrh. Obas sa aj poriadny divak uke.</p>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("wytkrsr5vqy95rl/17.webp")} alt="Starsi jelen v kukurici"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("iaee3es4daazxia/63_wildlife_kundrat.webp")} alt="Korunove jelene kukurica"/>

            <LoadableImage  className="rounded-sm shadow-sm" src={GetUri("ht90fk9wy4q37uc/jelenia_zver_sucho_kukurica.webp")} alt="Mlady jelen kukurica"/>

            <p>Pre zhrnutie uvediem, e jelene v tomto letnom obdob migruj z vych poloench lesov do miest s vyssiou koncentrciou vivnej potravy. Na bench miestach vskytu teraz jelene takmer nespozorujete. Ryhy su such, na hrebeni nealekej hory teraz nenjdete kvapku vody. V zime som tam pozoroval riedy o vekosti 20-50KS pri kadom vjazde.</p>

            <p>alou zaujmavosou je fakt, e extrmne sucho a vyschle zdroje vody vytvraj riedy jeleov v lete. Nieo podobne, ako je mon pozorova v zime. Jelene tak ostvaj v bliskosti vodnho zdroja v nizzsie poloench miestach. riedy s sprievodn jav sucha proste ak je malo vody s na mieste, kde voda je. Za inch podmienok by predpokladm nevznikli. Mono maximlne koly nadmernej potrave.</p>

            <p>Srnia zver bola v kukurici pozorovana minimlne, ba a vbec. Ta je omnoho viac aktvna prve v repke. Sporadicky sa objavila i v kukurici, no preferovala trvnat asti na okraji poa.</p>

            <p>Pokodenie rody kukurinho poa je enormne. Do konca leta tipujem viac ne 30-40 percent rastln plne pokodia. V tomto prpade ide o pole s rozlohou niekoko hektrov. V podstate vade s k videniu pokoden rastliny, oksi a podobne.</p>

            <p>Ja som, ale na strane jeleov a nech im chuti :)</p>
        </div>
    </>
}