import { useEffect } from "react";
import { Helmet } from "react-helmet";
import { GetUri } from "../../Constants";
import LoadableImage from '../UI/LodableImage';
import { PageViews } from "@piwikpro/react-piwik-pro";

 export default function NightWithBears(){

    useEffect(() => {
        PageViews.trackPageView('Noc s medvemi');
    }, [])

    return <>
        <Helmet>
            <title>Noc s medvemi</title>
            <meta
                name="description"
                content="Noc s medvemi"
            />
            <meta name="author" content="Dalibor Kundrat" />
            <meta
                name="keywords"
                content="medved, liptov medvede, fotenie medvede, video medvede"
            />
        </Helmet>
        <div className="space-y-5 text-base lg:text-lg">

            <h1 className="text-xl font-semibold">Noc s medvemi</h1>

            <p>U dlhiu dobu mm vyhliadnutych niekoko novch miest, kde by som chcel pozorova medvede. V okol hlavnho hrebea nzkych tatier s v podstate vade.</p>

            <p>Beriem bicykel a mierim do hory. Na mape mm niekoko miest, ktor si chcem prejs a lepie spozna.</p>

            <p>Jedno z miest je aj <i>"Marusova"</i> Postupujem zo zaiatku po zvnici neskr je to lesn chodnk. Po ceste ma poteia lezeck sektory mal portov skaly odistene borhakmi. Predstavujem si, o by urobil spolulezec ak by ho neakane navtvil maco. Asi by musel ma grigri a vytiahnu sa za mnou... Mono len privea rozmam...</p>

            <p>Po Krtkej dobe sa chodnk strati a ja kram aj s bajkom potokom, ktor st na mal horsk lku. Ta vyzer ako z inho obdobia lopchy o vekosti stolov a porast miestami vye hlavy. Ach nao som sa tu vybral na bicykli? Hne si odpovedm slapat 15km peo by sa mi sem nechcelo a asu som uetril a a.</p>

            <p>Prechdzam na aliu lku, kde miestne zdruenie prve buduje nov posed. Poloha je luxusn udujem sa, e ho tu dodnes nemali. Nakoko tu nenachdzam miesto, kde by som mohol bezpene bivakova vol presun na in lokalitu asi 10km vzdialen.</p>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("sti8qi9c2004k3j/44.webp")} alt="Vyhlad na horskom chodniku"/>

            <p>Cestou natrafm na nejak to varovanie priamo na zvnici :)</p>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("niialgqr7zxq6n9/48.webp")} alt="Medvedie hovno"/>

            <p>10km v tunajom terne znamen "hore, dole, hore" ni, o by som si uval.</p>

            <p>Prichdzam na aliu horsk lku, kde som u v minulosti bol je tu posed s idelnym vhadom na vetky strany. D mi dostatok bezpeia cez noc.</p>

            <p>Naposledy som tu fotil jelene so zaujmavm sfarbenm.</p>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("40pqhkohgwb2pyy/26.webp")} alt="Zimno-letne sfarbenie mladeho jelena"/>

            <p>Pomaly zana zapada slnko ja dojedm veeru a pripravujem sa, e mono nieo aj odfotm. Prroda mauje ndhern farby.</p>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("objf9ei6d3ksll0/49.webp")} alt="Vyhlad z horskej luky"/>

            <h4 className="font-semibold"># Po zotmen</h4>

            <p>Ako prv vidm srniu. Ta, ale po chvli had do av a utek pre. Vravm si asi ide nejak poovnk alebo niekto kto zabldil? U po minte zisujem, o ich vyplailo. Bol to maco.</p>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("36gvd0qa74qzriw/medved_kundrat.webp")} alt="Mlady medved"/>

            <LoadableImage className="rounded-sm shadow-sm" src={GetUri("nat80pdjgpi6mbe/maco_liptov_kundrat.webp")} alt="Maco video"/>

            <p>Je po 9 veer slnko u zapadlo, a to je ich as. Foti sa ned tak nahrvam aspo krtke video, aby som si to sptne vedel pripomen a zanalyzova. Aj, ke bude zrnit, vbec nevad.</p>

            <p>Maco postupne prichdza bliie do mjho zornho poa, neskr ma dokonca aj zevetri a odbehne do nealekej mladiny zkade onedlho vybehne sp na lku.</p>

            <div className="w-full justify-center content-center">
                <iframe
                    className="w-full h-80 md:h-96 xl:h-128"
                    src="https://www.youtube.com/embed/HlMtZfrMCYE" 
                    title="Liptovsky maco" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen />    
            </div>

            <p>Je pol desiatej a nastala tma. Mesiac je niekde schovan nevidno absoltne ni. Len pou medvea, ktorho som natal, ako rob v okol hluk. Ja idem spa</p>

            <p>Budm sa po polnoci na brechot srnej. V okol posedu poujem pohyb. Vyahujem elovku a svietim si v inak plnej tme.</p>

            <p>V okol sa napchvaj 4 medvede, plne ignoruj svetlo hlavu maj dolu a id ako kosaky.</p>

            <p>Budm sa o 3 rno, ke zana byt ero. Pred tvrtou u svit a vtky pospevuj. Medvede u nevidie, akoby sa po nich zahla zem. Ani srnia ani jelene.</p>

            <p>Rno je hluch no nevad. O 5 rno balm veci a spam sa po zvnici dole do dolia k miestnej vajcovke, ktorou pouvaj, ako termanly prame a jazierko.</p>

            <p>Mam nohy, otuujem sa a jem nejak zvyky, o my ostali z veere. Dvam si do cieu sem ete niekoko krt prs...</p>

        </div>
    </>
}