import { useEffect } from "react";
import { Helmet } from 'react-helmet';
import { GetUri } from "../../Constants";
import LoadableImage from '../UI/LodableImage';
import { PageViews } from "@piwikpro/react-piwik-pro";

export default function IFoundTheDog(){

    useEffect(() => {
        PageViews.trackPageView('Pes priatel loveka');
    }, [])
    
    return <>

        <Helmet>
            <title> Libor</title>
            <meta
                name="description"
                content="Pes Libor - Problematika tulavch zvierat"
            />
            <meta 
                name="author" 
                content="Dalibor Kundrat" 
            />
            <meta
                name="keywords"
                content="tulavy pes, tulave zviera, polovnicky pes"
            />
            <meta
                property="og:title"
                content="Pes, priate loveka"
            />
            <meta 
                property="og:description" 
                content="Pes, priate loveka - Problematika tulavch zvierat" 
            />
            <meta property="og:type" content="article" />
        </Helmet>

        <div className="space-y-5 text-base lg:text-lg text-justify">
        
            <h1 className="text-xl md:text-2xl font-bold"> Libor</h1>
            
            <LoadableImage className="rounded-sm" src={GetUri("h3tyvjvmtcxkvll/liborko.webp")} alt="Libco"/>

            <br/>
            
            <h2 className="text-lg font-semibold">V skratke:</h2>

            <div className="flex flex-col">
                <TimelineItem order={1} name="Naiel som psa">Vyhladovan, zamotan tulk</TimelineItem>
                <TimelineItem order={2} name="Psia trauma">Brani sa kontaktu, upnutiu no inak je to maznk</TimelineItem>
                <TimelineItem order={3} name="Problm tulkov">Problm njs tulok, komunikcia s tulkami</TimelineItem>
                <TimelineItem order={4} name="tulok">tulok chce bezdvodne psa utrati</TimelineItem>
                <TimelineItem order={5} name="Druh anca">Presun psa do doasnej starostlivosti</TimelineItem>
                <TimelineItem order={6} name="Vchova">Pes nem nvyky. U sa povelom a chovaniu</TimelineItem>
                <TimelineItem order={7} name="Stratil sa">Silvestrovsky tek</TimelineItem>
                <TimelineItem order={8} name="Naiel sa">Ke nik neveril tak sa naiel</TimelineItem>
            </div>

            <br/>

            <HeaderSection id="1"> Naiel som psa</HeaderSection>

            <p>Jeden podveer pri nvrate z fotenia nachdzam na okraji lesa nakos vyhladovanho psa.</p>

            <LoadableImage className="rounded-sm" src={GetUri("pwqu395qnsjyhts/Libor.webp")} alt="Libor pri najdeni"/>

            <p>Okolo krku ma drten lanko, je zamotan... Pokus da mu to dole je nespen reaguje obrann, to sa dookola a mot sa ete viac... Je vystraen, nedveriv... Pska nechvam na mieste a vyberm sa po jedlo. Po tme sa vraciam sp a za pomoci jedla ho iastone vymotm a beriem sebou...</p>

            <LoadableImage className="rounded-sm" src={GetUri("mjgromiidhikcl5/Libor2.webp")} alt="Libor prve jedlo"/>
            
            <p>Hrzol, ke som mu dval dole to, o za sebou ahal... Vidm, e sa bl a mm, preto pochopenie... Mimo priameho kontaktu je kudn a len postava a po cca. 2h si ahne na provizrny pelech a spi.</p>

            <br/>

            <HeaderSection  id="2"> Psia trauma</HeaderSection>

            <p>Nasledujce dni zisujem, e pes si musel niem prejs... udia tlav zviera astokrt nenahlsia, nepomu a ke sa pes chova inak, najradej by ho nechali odstreli...</p>

            <p>Privolan veterinr s mojou pomocou zisti, e psk nieje ipovan a informuje o nutnosti karantny po dobu 14dni. No iadne bliie vyetrenie sa nekon.</p>

            <LoadableImage className="rounded-sm" src={GetUri("nzx02j9qcwclk5g/libor3.webp")} alt="Libor provizorny pelech"/>

            <p>Psk sa nechce necha upn. Na oblas krku a zadku reaguje obrann. Viem v akom stave som ho naiel a mm, preto pochopenie...</p>

            <p>Nentim mu to z oho ma traumu a chodm s nim navolno aj na dlh prechdzky 10-15km odoma sa nevzdiali...</p>
            
            <p>Postupne zaina nabera na vhe no je to beh na dlh tra.</p>
            <br/>
           
            <HeaderSection id="3"> Problematika tlavch zvierat</HeaderSection>


            <p>Od prvho da som kontaktoval okolit PZ a obce nakoko sa me jedna o zatlanho psa z okolia. K psovi sa nik nehlsil...</p>

            <p>Pomaly zisujem ak je problematika tulkov. Obec mala o psa postaran (mnou) a z 5 oslovench tulkov sa ozvali len dva so slovami, e s plne a ni neber. Ty, o maj povinnos zobra nezdvhaj telefn a je to jeden zaarovan kruh.</p>

            <p>Po vye tdni tlam na starostu, e problm musme riei nakoko nie som miestny a potreboval som ods no, o bude so psom?</p>

            <p>Z okolia povam <i className="italic">"Nao si ho sem bral...</i>  A podobne <i className="italic">"Drsty"</i>, ktor nemaj iadnu pridan hodnotu akurt bezvznamn hundranie a zrkadlo na povahu ud...</p>

            <p>Po niekokch telefontoch a doch sa podar vybavi tulok pomocou hygieny a ten psa preberie (fakturuje obci prebratie a kilometre)</p>

            <LoadableImage className="rounded-sm" src={GetUri("olirt00lctzs04w/libor_odchyt.webp")} alt="Libor presun do utulku"/>
            <br/>

            <HeaderSection id="4"> Nemenovan tulok</HeaderSection>


            <p>Pre niekoho by to tulkom skonilo no ma zaujmalo, o s nim je a pri predani tulku som veril, e njde nov domov.</p>

            <p>Strnky tulku hrde vravia <i className="italic font-semibold">"My nechcene psi neutrcame"</i>. Moja predstava a realita bola, ale plne odlin.</p>

            <p>Po 3 tdoch som navtvil tulok, e psa pjdem veni a pozriem, ako sa mu dar, poprpade zistm ak je postup, pretoe to vetko je premena nov sksenos.</p>

            <LoadableImage className="rounded-sm" src={GetUri("tqraasm403gnpf3/libor_utulok.webp")} alt="Libor v utulku"/>

            <p>Pracovnka spomna, e oni adoptuj pskov pre deti a do zahraniia. Nechpem plne, ako si to mm vysvetli, ale v ten moment to hdem za hlavu...</p>

            <p>Po vstupe do tulku bree 240 psov na malom pozemku. Je to neopsaten pocit.  Vetky akoby hovorili <i className="italic">"Chcem s pre, vezme si ma?"</i>.</p>

            <p>Onedlho prichdzame k psovi a ako jedin sn nebree ani ho nieje vidie... Je skryt v bdke a neche ani vyjs von.</p>

            <p>Takho ho nespoznvam. Kde je ten pes, ktor sa te, otravoval, bol tvrdohlav? Vidm vystraenho psa, ktorho lkame na konzervu s msom, aby vbec vyiel von. Takto sa nebl ani, ke som ho ja naiel...</p>

            <br/>
            
            <p>Po vye 2 tdoch prichdzam op. Psk ma v koterci minimlne 5 hovien na 2m2 a cely zadok pinav (zaschnute kusy hliny a hovien)..</p>
         
            <LoadableImage className="rounded-sm" src={GetUri("zyy9tex7pj55uio/libor_utulok2.webp")} alt="Libor vencenie utulok"/>

            <p>Pri nvrate mi pracovnka vrav.. <i className="italic">"Nabudce radej dopredu dajte vedie, e prdee... Mme v plne ho da utrati... </i>Vysvetuje to, o vraveli aj naposledy, e oni adoptuj pskov pre deti a do zahraniia. A on je u star psk...</p>

            <p>Odpovedm, e chpem, e si ho tam nik nevimne pri tom mnostve psov no poukazujem, e on nieje ani pripusteny k adopcii... Nieje ani na strnkach, neexistuje...</p>

            <p>Zamyslel som sa, e vlastne vetky psy v tulku boli zdrav a strnky astokrt ukazuj zchrann psy hrobrovi z lopaty no tak tam veru neboli... V ten moment som vedel, e som mu nijak nepomohol...</p>

            <p>Ako ste si vimli nemenujem konkrtny tulok... Preo ?? Pretoe boli jedin, ktor ho vtedy vzali, pretoe nato me doplati len ich zvynch vye 200 psov, ktor, zato nemu a s tam umiestnen. Business za odchyt sa, ale nem robi... Inak to nepln el OZ, ktor hlsaj a za ktor dostvaj dary od ud...</p>

            <p>Ak by ubehol rok a viac mono by som to pochopil, ale po mesiaci po umiestnen to ma ozaj mrzelo, pretoe premena to bol ten pes, ktorho som naiel na pokraji sl.</p>
            <br/>

            <HeaderSection id="5"> Druh anca</HeaderSection>


            <p>Nenapad ma ni in, ako op sksi niekoho kontaktova a poksi sa mu njs nov domov. Inak som mu nijak nepomohol.</p>

            <p>Kontaktujem inde OZtko, ktor nemaj tulok a s vlune na <i className="italic">"Doaskach"</i> (O psy sa staraj dobrovonci u seba doma).. Vysvetujem situciu a shlasia, e mu spolon njdeme nov domov v R. Pokia sa mu nenjde domov bude umn v docaske.</p>

            <p>Ete veer pred prevzatm volm do pvodnho tulku nech ho rno nachystaj na odvoz, e ho vyzdvihnem...</p>

            <p>Pri prevzat dostvam do rk veterinrny preukaz s dtumom, e pes bol okovan a ipovan de pred prevzatm (ke som im volal, e po prdem)... To samozrejme malo byt uroben v ase, ke ho prijali a nie mesiac po.</p>

            <p>Hne prv de prichdza hnackovanie, neskr krv v stolici a ide sa k vetrinarke na inekcie.</p>

            <p>S odstupom asu a niekolkzch druhoch jedla a granl konme u hypoalergennych, po ktorch ma konene trvenie v poriadku...</p>
            <br/>

            <HeaderSection id="6"> Vchova</HeaderSection>

            <p>Pes bez nvykov sa u zkladnm povelom a chovaniu a hlavne neura na vetko, o vid...</p>

            <p>Netrv to dlho a pes sa rchlo adaptuje problmy s upnutim s fu, u neur pta sa na potrebu, je vernm spolonkom pri dlhch prechdzkach lesom.</p>

            <p>Nakoko fotm, leziem a trackujem stal sa mojim stalym parakom. Vlastne ani ho nieje kam odloi...</p>

            <LoadableImage className="rounded-sm" src={GetUri("bopjo64h3txn2st/luka.webp")} alt="Libor luka"/>

            <LoadableImage className="rounded-sm" src={GetUri("gnpfzwxjgem2gls/libor_ta3.webp")} alt="Libor hory"/>

            <br/>

            <HeaderSection id="7"> Stratil sa</HeaderSection>

            <p>Ubehol as a pes sa socializoval. S prichdzajcim Silvestrom padol pln vyhn sa ohostrojom, tekom vysoko a aleko od toho treskotu na hrebe hory.</p>

            <p>Pes v spolonosti harajucej fny... Ani jeden z psov sa nevzdialil a cely as pobehovali, pohoda veer.</p>
            
            <LoadableImage className="rounded-sm" src={GetUri("4pla3x2dx640g7q/libor_silvester.webp")} alt="Libor silvester"/>

            <SubHeaderSection> 1.1.2023 00:00</SubHeaderSection>

            <p>Je polnoc, v diake pou ohostroje...   Slabo no aj to stailo, aby sa Libor vyplail...</p>

            <p>Hadme ho, unaven to vzdvam okolo 03: 00 rno. elovka sa vybila, mobil takmer tie. </p>

            <p>Od rana hadm v okol, vyberm sa pomaly lesom dole no pesa nikde...</p>

            <p>Poobede sa vrtim sadnem k PC a poda mapy urujem miesta, kde by mohol zbehn. Kontaktujem obce, aby ma informovali ak by sa pes ukzal.</p>

            <SubHeaderSection> 2.1.2023</SubHeaderSection>

            <p>Vstvam obvam beeck body. plne naahko v batohu nudzovka a vyberm sa hore na hrebe, e to sksim prebehn...</p>

            <p>Som desiatky metrov od hlavnho hrebea miesta, kde sa stratil. Pln bol zbehnu na opan stranu do doliny.... No v tom prichdza telefont, e ho zachytila fotopasca...</p>

            <LoadableImage className="rounded-sm" src={GetUri("y876lupxde7fpu5/fotopasca.webp")} alt="Fotopasca"/>

            <p>Smer cca. 10km po hlavnom hrebeni... Chod po vlch chodnkoch, kde ma partia z lesoochranarskeho zdruenia fotopasce...</p>

            <p>Utekm po hrebeni, na mieste, kde som mal odboi do nealekho sedla (kde je fotopasca) stretvam miestneho poovnka...</p>

            <p>Ukazuje mi fotky, e ho pred  2h mal v abe. Skutone je to on... Ukazuje na dolinu a opisuje miesto, kde sa stretli...</p>

            <LoadableImage className="rounded-sm" src={GetUri("4qqguq5otpgwykt/polovnik.webp")} alt="Polovnik"/>

            <p>Tunajie doliny nies iadna sranda. Strm kopce, mnoh s slep a na vrchu sneh a ad.</p>

            <p>Cestou natrafm na stopy, ktor potvrdzuj miesto, kde sa s nim mal poovnk.</p>

            <p>Prichdzam na hranicu lesa nad obcou Kre, kde su chaty a ptam sa chatrov i tu nevideli pobehova psa... Skutone tu pred 1-2h pes pobehoval...</p>

            <p>Preiel vek as pohoria. Pohyboval sa po vlch chodnkoch a po zbehnuti som stratil info... Prila tma a de sa skonil...</p>

            <p>Bol som od neho 1-2h asovo no nepodarilo sa... Veer vytvram trojuholnk oblasti kam me poda ternu s...</p>

            <p>Pridvaj sa super udia, e pjdu rno hada so mnou a kad pjde jeden smer... Okolit obce prisbili, e ho vyhlsia v rozhlase...</p>

            <SubHeaderSection> 3.1.2023</SubHeaderSection>

            <p>Vyrame do obce Krze, kde bol viden naposledy. Je to dos od ruky prichdzame a zaname prehadva okolie... Vyberm sa k vedajej obci cez kopec...</p>

            <p>Odvera ho nik nehlsi... Je obed kad to pomaly vzdva... Som v obci Livov rozprvam sa s miestnymi poovnkmi a vlastne som to u vzdal.</p>

            <p>V tom prde telefont, e ho videli u lesa nad obcoc kde bol aj vcera podvecer... Ja volm poovnkovi z Krov nech tam zabehne...</p>
            <br/>

            <HeaderSection id="8"> Naiel sa!</HeaderSection>

            <p>Treba veri do poslednej chvle aj tak sa stalo. Bol samozrejme hladn... Kos, ktor mu hodili nechcel necha a malinko si aj povrcal.</p>

            <p>Po naloen do auta len leal a po prchode ho hne vyplo a prespal cely zvyok dna...</p>
            
            <br/>

            <LoadableImage className="rounded-sm" src={GetUri("n4ercvpfjzheljp/libor_sneh.webp")} alt="Libor sneh"/>

            <LoadableImage className="rounded-sm" src={GetUri("t77zefsdiiqmomz/libor_ta3_2.webp")} alt="Libor na snehu"/>

            <LoadableImage className="rounded-sm" src={GetUri("t6elvl55dbcl3tv/libor_sneh2.webp")} alt="Libor na snehu"/>

            <LoadableImage className="rounded-sm" src={GetUri("k6wx11qsx0tolx4/libor_cergov.webp")} alt="Libor na snehu"/>


        </div>
    </>
}


//-------------------------------------------

type HeaderSectionProps = {
    children:string
    id?:string | undefined
}

function HeaderSection({children,id}:HeaderSectionProps){
    return <h2 id={id} className="font-bold h-10 bg-gray-400 items-center flex  px-5 text-black sticky top-14 z-10">
        {children}
    </h2>
}

//-------------------------------------------

type SubHeaderSectionProps = {
    children:string
}

function SubHeaderSection({children}:SubHeaderSectionProps){
    return <h4 className="font-bold h-6 items-center flex rounded-md text-base w-44">
        {children}
    </h4>
}

//-------------------------------------------

type TimelineItemProps = {
    order:number
    name:string
    children:string
}

function TimelineItem({order,children,name}:TimelineItemProps){

    const handleClickScroll = () => {
        const element = document.getElementById(order.toString());
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      };

    return <div onClick={handleClickScroll} className="h-14 cursor-pointer flex flex-row space-x-10 items-center flex-nowrap group">

        <div className="flex relative h-14 select-none">
            <div className="w-0.5 h-14 bg-white whitespace-pre absolute left-3"/>
            <div className="w-6 h-6 bg-white absolute top-3 rounded-full overflow-hidden items-center flex">
                <div className="m-auto text-sm text-black text-center leading-none justify-center z-10">
                    {order}
                </div>
            </div>
        </div>
        
        <div className="my-auto flex flex-row relative text-base space-x-3 md:space-x-5 items-center">
            <div className="font-semibold text-center flex flex-nowrap whitespace-pre">{name}</div>
            <div></div>
            <div className="text-gray-200 group-hover:underline whitespace-pre-wrap text-left">{children}</div>
        </div>
    </div>
}